import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AppMin } from '../core'
@Component({
  selector: 'app-user-note',
  templateUrl: './user-note.page.html',
  styleUrls: ['./user-note.page.scss'],
})
export class UserNotePage implements OnInit {
  items = []
  page = 0;
  header_class;
  disabled = false;
  constructor(public Router: Router, public AppMin: AppMin) {

  }
  doRefresh(refresher) {
    this.page = 0;
    this.items = [];
    this.addNote(refresher);
  }
  doInfinite(infiniteScroll) {
    this.addNote(infiniteScroll);
  }

  addNote(infiniteScroll?) {
    let obj = {
      type: "getUserNote",
      page: this.page + 1,
      toUserId: this.AppMin.getUserid(),
    }

    this.AppMin.get((obj)).subscribe((data) => {

      // this.items = data['rows'];
      for (var i = 0; i < data['rows'].length; i++) {
        this.items.push(data['rows'][i]);
      }
      this.page = data['page'];

      if (data['rows'].length == 0) {
        this.disabled = true;
      }


      if (infiniteScroll) {
        infiniteScroll.target.complete();
      }


    })

  }

  ngOnInit() {
    this.addNote();
  }

}
